.stationWrapper {
  width: 100vw;
  height: 100vh;
  background: -webkit-linear-gradient(
    212deg,
    #fd008f,
    #9700fd,
    #003dfd,
    #007afd,
    #05c7e6
  );
  background: -o-linear-gradient(
    212deg,
    #fd008f,
    #9700fd,
    #003dfd,
    #007afd,
    #05c7e6
  );
  background: linear-gradient(
    238deg,
    #fd008f,
    #9700fd,
    #003dfd,
    #007afd,
    #05c7e6
  );
  background-size: 1000% 1000%;
  -webkit-animation: rainbow 30s ease infinite;
  animation: rainbow 30s ease infinite;
  background-attachment: fixed;
  text-align: center;
  overflow: hidden;
}

.station {
  padding: 0 8vw 0 8vw;
  margin-top: 24px;
}

.station-header {
  height: 12vh;
  display: -ms-flexbox;
  display: flex;
  margin-top: -1rem;
}

.station-header .back-button-container {
  height: 2.6rem;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-item-align: center;
      align-self: center;
  display: -ms-flexbox;
  display: flex;
}

.station-header .back-button-container .back-button {
  height: 100%;
  -ms-flex-item-align: start;
      align-self: flex-start;
}

.station-header .logo {
  -ms-flex-item-align: center;
      align-self: center;
  width: auto;
  height: 70%;
}

.station-header .empty {
  -ms-flex: 1 1;
      flex: 1 1;
}

.lw-container {
  height: 15vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.lw-container .location {
  -ms-flex-item-align: center;
      align-self: center;
  text-align: left;
