      align-self: flex-start;
}

.measurement-container .text-values .right {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.measurement-container .text-values .right h3 {
  -ms-flex-item-align: end;
      align-self: flex-end;
}

.measurement-container .text-values h3:nth-child(2) {
