.aboutUsWrapper {
  background: -webkit-linear-gradient(
    212deg,
    #fd8800,
    #fd008f,
    #9700fd,
    #003dfd,
    #05c7e6,
    #4bd58d
  );
  background: -o-linear-gradient(
    212deg,
    #fd8800,
    #fd008f,
    #9700fd,
    #003dfd,
    #05c7e6,
    #4bd58d
  );
  background: linear-gradient(
    238deg,
    #fd8800,
    #fd008f,
    #9700fd,
    #003dfd,
    #05c7e6,
    #4bd58d
  );
  background-size: 1200% 1200%;
  -webkit-animation: rainbow 30s ease infinite;
  animation: rainbow 30s ease infinite;
  background-attachment: fixed;
  text-align: center;
  height: 100%;
}

.about-us-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-direction: column;
      flex-direction: column;
}

.about-us-container > div {
  padding-bottom: 1.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  display: -ms-flexbox;
  display: flex;
}

.about-us-container .description {
  text-align: justify;
  -ms-flex-direction: row;
      flex-direction: row;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
  margin-top: 0.5rem;
}

.about-us-container .text {
  padding: 1rem;
  text-align: left;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.about-us-container div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-direction: row;
      flex-direction: row;
}

.about-us-container img {
  height: 6rem;
}

.about-us-container .text a {
  color: white;
}

@media screen and (max-width: 370px) {
  .about-us-container .text {
    font-size: 0.9rem;
  }
  .about-us-container img {
    height: 4.5rem;
  }
}



