img.flowflow-logo {
  width: 35vw;
  max-width: 250px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1rem;
  display: block;
}

img.flowflow-logo.focused {
  width: 18vw;
  max-width: 125px;
  margin-top: -70px;
}

.mobile-safari-hidden {
  display: none;
}

.mainWrapper {
  width: 100vw;
  height: 100vh;
  position: fixed;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
  background: -webkit-linear-gradient(
    212deg,
    #fd8800,
    #fd008f,
    #9700fd,
    #003dfd,
    #05c7e6,
    #4bd58d
  );
  background: -o-linear-gradient(
    212deg,
    #fd8800,
    #fd008f,
    #9700fd,
    #003dfd,
    #05c7e6,
    #4bd58d
  );
  background: linear-gradient(
    238deg,
    #fd8800,
    #fd008f,
    #9700fd,
    #003dfd,
    #05c7e6,
    #4bd58d
  );
  background-size: 1200% 1200%;
  -webkit-animation: rainbow 30s ease infinite;
  animation: rainbow 30s ease infinite;
  background-attachment: fixed;
  text-align: center;
}

.slogan {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.slogan-hidden {
  display: none;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.footer > p {
  -ms-flex-item-align: start;
      align-self: flex-start;
  padding-right: 1rem;
  text-decoration: underline;
  cursor: pointer;
}

.fish1 {
  z-index: -10;
  top: 230px;
  height: 50px;
  width: 50px;
  left: -150px;
  position: relative;
  -webkit-animation-name: move;
          animation-name: move;
  -webkit-animation-duration: 8s;
          animation-duration: 8s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
}

@-webkit-keyframes move {
  0% {
    -webkit-transform: translate(-150px, 0);
            transform: translate(-150px, 0);
  }

  40% {
    -webkit-transform: translate(500px, -50px);
            transform: translate(500px, -50px);
  }

  100% {
    -webkit-transform: translate(500px, -50px);
            transform: translate(500px, -50px);
  }
}

@keyframes move {
  0% {
    -webkit-transform: translate(-150px, 0);
            transform: translate(-150px, 0);
  }

  40% {
    -webkit-transform: translate(500px, -50px);
            transform: translate(500px, -50px);
  }

  100% {
    -webkit-transform: translate(500px, -50px);
            transform: translate(500px, -50px);
  }
}



